:root {
  --font1: "Open Sans", sans-serif;
  --font2: "Raleway", sans-serif;
  --color1: #08a6e4;
  --color2: #a5a5a5;
  --color3: #fb3b64;
  --color4: #55dfb4;
  --color5: #07ace6;
  --color6: #e8e9ed;
  --color7: #13253b;
  --shadow1: rgba(0, 0, 0, 0.16);
  --shadow2: rgba(7, 172, 230, 0.16);
}

body {
  box-sizing: border-box;
}

.page-header {
  height: 70px;
  background-color: #fff;
  box-shadow: 0 3px 6px var(--shadow1);
}
.page-header .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.container {
  max-width: 1352px;
  min-width: 900px;
  height: 100%;
  margin: 0 auto;
}

.page-header__logo {
  margin: 0;
  color: var(--color1);
  font-family: var(--font1);
  font-size: 32px;
  font-weight: 300;
}

.page-header__nav {
  height: 100%;
}

.page-header__nav--list {
  list-style: none;
  padding: 0;
  margin: 0;
  height: 100%;
  display: flex;
}

.page-header__nav--el {
  height: 95%;
  margin: 0;
}
.page-header__nav--el:last-child a {
  margin-right: 0;
}
.page-header__nav--el:hover .page-header__nav--link {
  border-bottom: 4px solid var(--color1);
  font-weight: bold;
  color: #000;
}

.page-header__nav--link {
  margin: 0;
  padding: 0 10px;
  height: 95%;
  font-family: var(--font2);
  font-size: 11px;
  color: var(--color2);
  text-decoration: none;
  margin: 4px 40px;
  text-transform: uppercase;
  letter-spacing: 3.28px;
  font-weight: bold;
  display: flex;
  align-items: center;
}

.header__nav--active {
  border-bottom: 4px solid var(--color1);
  font-weight: bold;
  color: #000;
}

.section-opening {
  height: 842px;
  background: url(../assets/background.png) no-repeat center bottom;
}
.section-opening .container {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}

.section-opening__title {
  margin-top: 130px;
  margin-bottom: 20px;
  text-align: center;
  font-family: var(--font2);
  font-size: 49px;
  color: #000;
  font-weight: 400;
}

.section-opening__text {
  font-family: var(--font1);
  font-size: 16px;
  color: #000;
  font-weight: 400;
  margin-bottom: 38px;
}

.section-opening__link {
  width: 440px;
  height: 67px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: none;
  border-radius: 40px;
  background-color: var(--color1);
  text-decoration: none;
  color: white;
  font-family: var(--font1);
  font-size: 16px;
  font-weight: 700;
  transition: 0.1s;
  box-shadow: 5px 10px 14px var(--shadow2);
}

.section-stats {
  height: 531px;
  box-shadow: 0 -5px 80px var(--shadow1);
}

.section-stats__title {
  font-family: var(--font2);
  font-size: 36px;
  text-align: center;
  color: #000;
  margin: 0;
  padding: 100px 0;
  font-weight: 400;
}

.section-stats__selection {
  margin: 0 auto;
  display: flex;
  justify-content: space-around;
  align-items: stretch;
}

.section-stats__box {
  width: 150px;
  margin: 0 100px;
  display: flex;
  flex-direction: column;
  justify-items: space-around;
  align-items: center;
}

.section-stats__box--icon {
  width: 63px;
  height: 63px;
  border-radius: 50%;
  position: relative;
}

.stats__icon--person {
  background-color: var(--color3);
}
.stats__icon--person::after {
  content: "";
  position: absolute;
  height: 60px;
  width: 65px;
  left: 30px;
  top: 2px;
  background: url(../assets/person.svg) no-repeat;
}

.stats__icon--eye {
  background-color: var(--color1);
}
.stats__icon--eye::after {
  content: "";
  position: absolute;
  height: 60px;
  width: 65px;
  left: 30px;
  top: 15px;
  background: url(../assets/eye.svg) no-repeat;
}

.stats__icon--catalog {
  background-color: var(--color4);
}
.stats__icon--catalog::after {
  content: "";
  position: absolute;
  height: 60px;
  width: 65px;
  left: 30px;
  top: 5px;
  background: url(../assets/catalog.svg) no-repeat;
}

.section-stats__box--value {
  font-family: var(--font2);
  color: #000;
  font-size: 20px;
  font-weight: 800;
  text-align: center;
  letter-spacing: 5.96px;
}

.section-stats__box--note {
  font-family: var(--font2);
  color: var(--color2);
  font-size: 16px;
  font-weight: 400;
  text-align: center;
  margin-top: 0;
}

